.Countdown {
  position: relative;
}

.Countdown__time {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 30px;
}

.CountdownItem {
  border: 1px solid #D4D3D3;
  background-color: #fff;
  height: 134px;
  width: 115px;
  margin: 10px;
}

.CountdownItem__num {
  font-size: 60px;
  font-weight: bold;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

.CountdownItem__label {
  font-size: 18px;
  font-family: Helvetica, sans-serif;
  color: #999;
}

.Countdown__label {
  font-size: 18px;
  font-family: Helvetica, sans-serif;
  color: #999;
}

.Countdown--notstart .Countdown__time, 
.Countdown--notstart .Countdown__label{
  opacity: .23;
}

.Countdown__notstart {
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  font-size: 34px;
  font-weight: bold;
  color: #6F6F6F;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  line-height: 170px;
}