.Footer {
  background-color: #202124;
  color: #fff;
  font-size: 18px;
  font-family: Helvetica, sans-serif;
  height: 68px;
  line-height: 68px;
}

.Footer ul {
  text-align: right;
}

.Footer li {
  display: inline-block;
  margin-left: 20px;
}

.Footer li a {
  color: #fff;
  text-decoration: none;
}

.Footer li svg {
  margin-right: 10px;
}

.Footer__left {
  text-align: left;
}

.Footer__right {
  text-align: right;
}

ul, li {
  list-style: none;
  margin: 0;
  padding: 0;
}