/* Subtitle */
.Subtitle {
  display: flex;
  flex-direction: row;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  font-size: 36px;
  margin-top: 80px;
  margin-bottom: 60px;
  color: #2F75EC;
}

.Subtitle__title {
  margin-right: 20px;
}

.Subtitle__divider {
  flex: 1;
  padding-top: 29px;
}

.Subtitle__line {
  width: 100%;
  height: 1px;
  background-color: #D8D8D8;
}

.Subtitle--center {
  text-align: center;
}