.Purchase {
  border: 1px solid black;
  margin-top: 40px;
  /* box-shadow: 4 20 0 #000; */
}

.Purchase__header {
  background-color: #2F75EC;
  height: 109px;
  font-size: 64px;
  color: #fff;
  font-family: Helvetica, sans-serif;
  font-weight: bold;
  margin-bottom: 30px;
}

.Purchase__body {
  padding-left: 30px;
  padding-right: 30px;
}

.Purchase__label {
  color: #999999;
  font-size: 18px;
  font-family: Helvetica, sans-serif;
  margin-bottom: 30px;
}

.Purchase__num {
  font-size: 64px;
  font-weight: bold;
  font-family: Helvetica, sans-serif;
  color: #2F75EC;
  vertical-align: middle;
  line-height: 60px;
}

.Purchase__input input {
  font-family: Helvetica, sans-serif;
  font-size: 64px;
  color: #2F75EC;
  border: 0;
  background: transparent;
  line-height: 74px;
  font-weight: bold;
  padding-top: 0;
  padding-bottom: 0;
}

.Purchase__input input:focus {
  border: 0;
  outline: 0;
}

.Purchase__buy {
  border-top: 1px solid #D8D8D8;
  padding: 40px;
}

.Purchase__buy button {
  width: 100%;
  border-radius: 30px;
  padding: 10px;
  font-size: 28px;
  font-weight: bold;
  font-family: Helvetica, sans-serif;
}

.Purchase__winner {
  color: #FF5C5C;
  background-color: #FFEFF0;
  font-size: 34px;
  padding-top: 20px;
  padding-bottom: 20px;
}

.Purchase__row {
  margin-bottom: 30px;
}