.Reward > div{
  display: inline-block;
  text-align: center;
  vertical-align: middle;
}

.RewardCup {
  margin-right: 20px;
}

.RewardAmount {
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  font-size: 48px;
  color: #FF5C5C;
}

.RewardInfo {
  font-family: Helvetica, sans-serif;
  font-size: 18px;
  color: #999;
  font-weight: lighter;
} 

.RewardNum {
  line-height: 60px;
  vertical-align: middle;
}

.RewardNum__max {
  font-size: 72px;
}

.RewardNum__maxsuffix {
  font-size: 28px;
  font-family: Helvetica, sans-serif;
  font-weight: normal;
  color: #999;
  font-weight: lighter;
}

.Reward__chest {
  margin-right: 10px;
}