.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App__purchase {
  margin-bottom: 120px;
}

.Slogn {
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  font-size: 36px;
  margin-bottom: 60px;
}

.Body {
  max-width: 939px;
  margin: auto;
}

.Section {
  padding-top: 60px;
  padding-bottom: 60px;
  background-image: linear-gradient(#ffffff, #EFF4F6);
}

@media screen and (min-width: 992px) {
  .Section {
    padding-left: 200px;
    padding-right: 200px;
  }
}

.IntroReward {
  margin-top: 60px;
}